<template>
    <div class="m-bps-ladder-mini">
        <h3 class="m-ladder-title">
            <span class="u-title">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" v-bind:class="'u-icon'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><g fill="#ffa91e"><path d="M324.891 167.266l16.262 94.815L256 217.314l-85.153 44.767 16.262-94.815-68.891-67.16 95.205-13.833L256 0l42.577 86.273 95.205 13.833z"/><path d="M409.944 253.46v87.673L256.9 403.303l-.9-.36-153.944-61.76V253.46L256 315.219z"/><path d="M409.944 362.157v87.673L256.9 512l-.9-.36-153.944-61.76v-87.723L256 423.916z"/></g><g fill="#ff8900"><path d="M341.153 262.081L256 217.314V0l42.577 86.273 95.205 13.833-68.891 67.16zM409.944 253.46v87.673L256.9 403.303l-.9-.36v-87.724zM256 423.916l153.944-61.759v87.673L256.9 512l-.9-.36z"/></g></svg> 天梯榜
                <!-- <span class="u-dot">·</span> -->
            </span>
            <span class="u-more" @click="viewRank">查看更多<i class="el-icon-d-arrow-right"></i></span>
            <!-- <el-select v-model="zlp" placeholder="请选择" size="medium">
                <el-option v-for="item in zlps" :key="item.value" :label="item.label" :value="item.value"></el-option>
            </el-select> -->
        </h3>

        <div class="m-ladder-rank" v-loading="loading">
            <ul>
                <li v-for="(item, i) in rank" :key="i">
                    <div
                        class="u-item"
                        :style="{
                            width: getRate(item.dps),
                            backgroundColor: xfcolor(item.xf),
                        }"
                    >
                        <img :src="showMountIcon(xfmap[item.xf]['id'])" class="u-pic" />
                        <template v-if="!!item.link">
                            <a class="u-text" :href="item.link" target="_blank">
                                {{ item.xf }}
                                <span class="u-desc" v-if="item.label">&lt;{{ item.label }}&gt;</span>
                            </a>
                        </template>
                        <template v-else>
                            <span class="u-text">
                                {{ item.xf }}
                                <span class="u-desc" v-if="item.label">&lt;{{ item.label }}&gt;</span>
                            </span>
                        </template>
                        <span class="u-dps">{{ item.dps }}</span>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import zlps from "@/assets/data/ladder.json";
import { authorLink, showAvatar, showSchoolIcon, showMountIcon } from "@jx3box/jx3box-common/js/utils";
import { getRank } from "@/service/ladder.js";
import xfmap from "@jx3box/jx3box-data/data/xf/xf.json";
import { colors_by_mount_name } from "@jx3box/jx3box-data/data/xf/colors.json";
export default {
    name: "MiniLadder",
    data() {
        return {
            zlp: "",
            // 排行
            xfmap,
            data: [],

            loading: false
        };
    },
    computed: {
        c: function () {
            return this.$store.state.client;
        },
        zlps: function () {
            return zlps[this.c] || [];
        },
        rank: function () {
            let data = this.data;
            data.sort((a, b) => {
                return ~~b.dps - ~~a.dps;
            });
            return data;
        },
    },
    mounted() {
        this.zlp = zlps[this.c][0]["value"];
        this.loadRank();
    },
    methods: {
        xfcolor: function (val) {
            return colors_by_mount_name[val];
        },
        getRate: function (val) {
            return ((val / this.maxBase) * 100).toFixed(2) + "%";
        },
        viewRank() {
            this.$router.push({ name: "Ladder" });
        },
        loadRank: function () {
            if (!this.zlp) return;

            this.loading = true;
            getRank(this.zlp, this.client)
                .then((data) => {
                    this.data = data;
                    this.$forceUpdate();
                })
                .finally(() => {
                    this.loading = false;
                });
        },
        authorLink,
        showAvatar,
        showSchoolIcon,
        showMountIcon,
    },
};
</script>

<style lang="less">
@import "~@/assets/css/ladder.less";
</style>
